import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
const HomepageContent = () => {
  return (
    <React.Fragment>
      <section className="contactme">
        <div className="contactme--works">
          My personal works @
          <span>
            <a
              href="https://codepen.io/aravi-pen"
              target="_blank"
              rel="noopener noreferrer"
              title="codepen"
              aria-label="codepen"
            >
              <FontAwesomeIcon icon={["fab", "codepen"]} />
            </a>
          </span>
          <span>
            <a
              href="https://stackoverflow.com/users/9002163/aravind-s"
              target="_blank"
              rel="noopener noreferrer"
              title="stackoverflow"
              aria-label="stackoverflow"
            >
              <FontAwesomeIcon icon={["fab", "stack-overflow"]} />
            </a>
          </span>
          <span>
            <a
              href="https://github.com/aravishack"
              target="_blank"
              rel="noopener noreferrer"
              title="github"
              aria-label="github"
            >
              <FontAwesomeIcon icon={["fab", "github"]} />
            </a>
          </span>
        </div>
        <div className="contactme--articles">
          My articles @
          <span>
            <a
              href="https://medium.com/@aravishack"
              target="_blank"
              rel="noopener noreferrer"
              title="medium"
              aria-label="medium"
            >
              <FontAwesomeIcon icon={["fab", "medium"]} />
            </a>
          </span>
        </div>
        <div className="contactme--email">
          If you’d like to get in touch —
          <a
            href="mailto:aravindsklm@gmail.com"
            title="gmail"
            aria-label="gmail"
          >
            <FontAwesomeIcon icon="envelope" /> me
          </a>
        </div>
      </section>
    </React.Fragment>
  );
};

export default HomepageContent;
