import React from "react";
const Loader = () => {
  return (
    <React.Fragment>
      <section id="loader">
        <div className="loading outer">
          <div className="loading inner" />
        </div>
        <div className="load-text">
          <span className="line-one">Breathe In...</span>
          <span className="line-one">Breathe Out...</span>
        </div>
      </section>
    </React.Fragment>
  );
};

export default Loader;
