import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
const Footer = () => {
  return (
    <footer className="footer">
      <div className="socials">
        <ul>
          <li>
            <a
              href="https://www.facebook.com/aravindsanthosh.18"
              target="_blank"
              rel="noopener noreferrer"
              title="facebook"
              aria-label="facebook"
            >
              <FontAwesomeIcon icon={["fab", "facebook"]} />
            </a>
          </li>
          <li>
            <a
              href="https://twitter.com/aravisanthosh"
              target="_blank"
              rel="noopener noreferrer"
              title="twitter"
              aria-label="twitter"
            >
              <FontAwesomeIcon icon={["fab", "twitter"]} />
            </a>
          </li>
          <li>
            <a
              href="https://www.instagram.com/aravi18/"
              target="_blank"
              rel="noopener noreferrer"
              title="instagram"
              aria-label="instagram"
            >
              <FontAwesomeIcon icon={["fab", "instagram"]} />
            </a>
          </li>
          <li>
            <a
              href="https://www.linkedin.com/in/aravindsanthosh/"
              target="_blank"
              rel="noopener noreferrer"
              title="linkedin"
              aria-label="linkedin"
            >
              <FontAwesomeIcon icon={["fab", "linkedin"]} />
            </a>
          </li>
        </ul>
      </div>
      <span>&copy; 2019 - code with &hearts; and ReactJS</span>
    </footer>
  );
};

export default Footer;
