import React, { useEffect, useState } from "react";
import mobbg from "../assets/mobilebg.jpg";
import Typed from "react-typed";
const Header = props => {
  const [clsName, setClsName] = useState("header--titlename");
  useEffect(() => {
    setClsName("header--titlename");
  }, []);
  return (
    <React.Fragment>
      <header className="header">
        <div className="header--mobile">
          <img src={mobbg} alt="background" />
        </div>
        <div className="header--title">Hey, I'm</div>
        <div className={clsName}>
          <span>A</span> <span>R</span> <span>A</span> <span>V</span>
          <span>I</span> <span>N</span> <span>D</span>
        </div>
      </header>
      <section className="aboutme">
        <Typed strings={props.options} typeSpeed={80} backSpeed={50} loop />
      </section>
    </React.Fragment>
  );
};

export default Header;
